// 处理字段映射相关

export interface MapItem {
  jsonML: string;
  value: string;
}

export const MAPPING: MapItem [] = [
  {
    jsonML: 'width',
    value: 'length',
  },
  {
    jsonML: 'type',
    value: 'lineType',
  },
  {
    jsonML: 'sz',
    value: 'size',
  },
  {
    jsonML: 'color',
    value: 'color',
  },
];

export interface IHrPayload {
  length?: string;
  lineType?: string;
  size?: number;
  color?: string;
}

export interface IHrASL {
  width: string;
  type: string;
  sz: number;
  color: string;
}

/**
 *
 * @param data slate value data
 * @param map 映射对象
 */
export const parseValueToJsonML = (data: IHrPayload, map: MapItem []): IHrASL => {
  const newObj: any = {
    width: null,
    type: null,
    sz: null,
    color: null,
  };
  if (!data) return newObj;
  map.forEach((item) => {
    newObj[item.jsonML] = data[item.value];
  });
  return newObj;
};

/**
 *
 * @param data jsonML data
 * @param map  映射对象
 */
export const parseJsonMLToValue = (data: IHrASL, map: MapItem []): IHrPayload => {
  const newObj: any = {
    length: null,
    lineType: null,
    size: null,
    color: null,
  };
  if (!data) return newObj;
  map.forEach((item) => {
    newObj[item.value] = data[item.jsonML];
  });
  return newObj;
};

